import React from 'react'
import Demo97 from './demo_97'

const Demolist97 = ({ demos }) => {
    return (
        <div>
            {demos.map((user,i) => {
                return (
                    <Demo97
                        key={i}
                        img={user.img}
                        title={user.title}
                        content={user.content}
                    />
                )
            })

            }
        </div>
    )
}

export default Demolist97

