import React from 'react';

const Demo97 = ({ img, title, content }) => {
    return (
        <div className='bg-pink dib br3 pa3 ma2 grow bw2 shadow-5 mh5'>
            <img src={img} alt="demo" className='mw5'/>
            <h2>{title}</h2>
            <p className='mw5'>{content}</p>
        </div>
    )
}

export default Demo97;