import React, { Component } from 'react'
// import { robots } from './robots'
import Demolist97 from '../Components/demolist_97';
import SearchBox97 from '../Components/searchbox_97';
import Scroll97 from '../Components/scroll_97';
import './App.css';
import { demos } from '../Components/demos_97';


class App extends Component {
    constructor() {
        super();
        this.state = {
            demos: demos,
            searchField: ''
        }
    }


    onSearchChange = event => {
        this.setState({ searchField: event.target.value });
    }

    render() {
        const filteredDemos = this.state.demos.filter(demo => {
            return demo.title.toLowerCase().includes(this.state.searchField.toLowerCase());
        });

        if (this.state.demos.length===0){
            return <h1>Loading...</h1>
        }else{
            return (
                <div className="tc">
                    <div style={{height: '120px' }}>
                        <h1>Class Demos.</h1>
                        <SearchBox97 searchChange={this.onSearchChange} />
                    </div>
                    <Scroll97>
                        <Demolist97 demos={filteredDemos} />
                    </Scroll97>
                </div>
            )
        }
    }

}

export default App

